export default {
	props: {
		bookDetail: {
			type: Object
		}
	},
	data() {
		return {
			isUnfold: false
		}
	},
	methods: {
		unfoldClick() {
			this.isUnfold = !this.isUnfold
		}
	},
	computed: {
		desc() {
			if(this.bookDetail.longIntro) {
				return this.bookDetail.longIntro.split('\r\n')
			}
		},
		tags() {
			if(this.bookDetail.tags.length > 6) {
				return this.bookDetail.tags.slice(0,6)
			}else {
				return this.bookDetail.tags
			}
			
		}
	}
}
